    @else if $point ==xl {
        @media only screen and (min-width: 1440px) and (max-width: 1599px) {
            @content;
        }
    }

    @else if $point ==rg&lg {

        ///////////////    mix    //////////////////////
        @media only screen and (min-width: 1024px) and (max-width: 1439px) {
            @content;
        }
    }
}

.outbound-dialog {
    background: rgba(0, 0, 0, 0.8);



    &__body {
        color: #000;

        p {
            font-size: 18px;
            line-height: 1.5;
            margin-top: 1em;
            margin-bottom: 1em;

            strong {
                font-weight: bold;
            }
        }
    }

    &__content {
        font-family: "Montserrat", sans-serif;
        max-width: 700px;
        margin: 0 auto;
        padding: 20px 60px;
        height: 90%;
        border-top: 30px solid #fff;
        border-bottom: 30px solid #fff;

        &:before {
            content: '';
            display: block;
            margin: 0 auto;
            height: 1px;
            width: 100%;
            background-color: #ccc;
            position: absolute;
            top: 0;
            left: 0;
        }
    }

    &__title {
        font-size: px(30);
        display: block;

        @include breakpoint(lg) {
            font-size: px(48);
        }

        font-family: "Montserrat",
        sans-serif;
        padding-bottom: 20px;
        margin: 0 auto;
        text-align: center;
        color: #000;
        display: flex;
        flex-direction: column;
        gap: 20px;
        margin: 0;
        padding: 0;

        &:after {
            content: '';
            display: block;
            margin: 0 auto;
            height: 2px;
            width: 160px;
            background-color: #ceb863;
        }
    }

    &__close {
        display: block;
        opacity: 1;
    }

    &__overlay-button {
        display: flex;
        justify-content: center;
        align-items: center;
        position: fixed;
        flex-direction: column;
        top: 10px;
        right: 10px;
        width: 17px;
        height: 17px;
        cursor: pointer;
        --rotate: 45deg;

        &:hover {
            --rotate: 0deg;
        }

        &:before {
            content: '';
            display: block;
            right: 10px;
            top: 20px;
            background-color: #fff;
            width: 17px;
            height: 2px;
            cursor: pointer;
            transform: rotate(calc(var(--rotate) * -1));
            transition: all 0.3s ease;
        }

        &:after {
            content: '';
            display: block;
            right: 10px;
            top: 20px;
            background-color: #fff;
            width: 17px;
            height: 2px;
            transform: rotate(var(--rotate));
            box-sizing: border-box;
            cursor: pointer;
            margin-top: -2px;
            transition: all 0.3s ease;
        }


    }
}